// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
/* eslint-disable */
import Vue from 'vue'
import App from './App'
import router from './router'
import 'iview/dist/styles/iview.css'
// eslint-disable-next-line
import { Button, Table, Card, Row, Col, Layout, Header, Menu, MenuItem, Content, Footer, Icon, Affix, BackTop, Tag, RadioGroup, Radio, Tabs, TabPane} from 'iview'

Vue.component('Card', Card)
Vue.component('Row', Row)
Vue.component('Col', Col)
Vue.component('Layout', Layout)
Vue.component('Header', Header)
Vue.component('Menu', Menu)
Vue.component('MenuItem', MenuItem)
Vue.component('Content', Content)
Vue.component('Footer', Footer)
Vue.component('Button', Button)
Vue.component('Table', Table)
Vue.component('Icon', Icon)
Vue.component('Affix', Affix)
Vue.component('BackTop', BackTop)
Vue.component('Tag', Tag)
Vue.component('RadioGroup', RadioGroup)
Vue.component('Radio', Radio)
Vue.component('Tabs', Tabs)
Vue.component('TabPane', TabPane)

import filters from './filter'
for(let key in filters)Vue.filter(key,filters[key])

import VueParticles from './components/vue-particles'
import './assets/tables.css'
Vue.use(VueParticles)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



