<template>
  <div id="app" class="layout">
    <Layout>
      <my-header></my-header>
      <my-content></my-content>
      <my-footer></my-footer>
    </Layout>
  </div>
</template>

<script>
import MyHeader from './components/MyHeader'
import MyContent from './components/MyContent'
import MyFooter from './components/MyFooter'

export default {
  name: 'App',
  components: {MyFooter, MyContent, MyHeader},
  data () {
    return {
    }
  },
  methods: {
    hideTitleInIt () {
      var OriginTitile = document.title
      var st
      document.addEventListener('visibilitychange', function () {
        if (document.hidden) {
          document.title = '藏好啦(つд⊂) ★' + OriginTitile
          clearTimeout(st)
        } else {
          document.title = '被发现啦(*´∇｀*)★' + OriginTitile
          st = setTimeout(function () {
            document.title = OriginTitile
          }, 4000)
        }
      })
    }
  },
  created () {
    this.hideTitleInIt()
  }
}
</script>

<style>
  html {
    overflow: scroll;
    overflow-x: hidden;
  }
  body {
    display: block;
    margin: 0;
    font-family: -apple-system,BlinkMacSystemFont,Helvetica Neue,PingFang SC,Microsoft YaHei,Source Han Sans SC,Noto Sans CJK SC,WenQuanYi Micro Hei,sans-serif;
    font-size: 15px;
    color: #1a1a1a;
    background: #f6f6f6;
    -webkit-tap-highlight-color:rgba(26,26,26,0);
  }
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .layout-footer-center{
    text-align: center;
  }
  #app{
    pointer-events: none;
  }
  #app .ivu-card{
    pointer-events: auto;
  }
  #app .ivu-layout-header{
    pointer-events: auto;
  }
  .touch{
    pointer-events: auto;
  }
</style>



