import Vue from 'vue'
import Router from 'vue-router'
import MyHome from '@/components/pages/MyHome'
import MyWork from '@/components/pages/MyWork'
import MyBlog from '@/components/pages/MyBlog'
import MyAbout from '@/components/pages/MyAbout'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      alias: '/home',
      name: 'MyContent',
      component: MyHome
    },
    {
      path: '/blog',
      name: 'MyBlog',
      component: MyBlog
    },
    {
      path: '/work',
      name: 'MyWork',
      component: MyWork
    },
    {
      path: '/about',
      name: 'MyAbout',
      component: MyAbout
    }
  ],
  mode: 'history'
})



