<template>
  <Row :gutter="18" type="flex" justify="center" align="top">
    <Col span="4">
    </Col>
    <Col span="16">
      <Card>
        <Tabs>
          <TabPane label="关于本站" class="markdown-body">
            <readme></readme>
          </TabPane>
          <TabPane label="关于作者" >暂无介绍</TabPane>
          <TabPane label="网站开发日记" class="markdown-body">
            <diary></diary>
          </TabPane>
        </Tabs>
      </Card>
    </Col>
    <Col span="4"></Col>
  </Row>
</template>

<script>
import Data from '../../../static/mork/blogs.json'
import Readme from '../../../README.md'
import Diary from '../../../static/mork/WebsiteDevelopmentDiary.md'
import 'highlight.js/styles/github.css'
import 'github-markdown-css'

export default {
  name: 'MyAbout',
  components: { Readme, Diary },
  data () {
    return {
      datas: [],
      readme: '',
      diary: ''
    }
  },
  created () {
    this.datas = Data
    this.readme = Readme
    this.diary = Diary
  },
  computed: {
    dataIsEmpty: function () {
      return this.datas.length !== 0
    }
  },
  methods: {
    finalImg: function (title) {
      return `../../static/mork/works/${title}/logo.png`
    },
    tocRender (value) {
      console.log(value)
    }
  }
}
</script>

<style scoped>
  .card-title{
    width: 20%;
    height: 0;
    margin-bottom: 20%;
    display:inline-block;
  }
  .card-title img{
    width:100%;
    height: 100vm;
  }
  .card-body{
    float: right;
    width: 70%;
  }
  .card{
    padding:20px;
  }
</style>



